import React, { Component } from 'react'
import Slider from 'react-slick'
import NouranPhoto from 'Shared/assets/images/other/nouran-elmedany.png'
import JonathanPhoto from 'Shared/assets/images/other/jonathan-paulson.png'
import MahitabPhoto from 'Shared/assets/images/other/mahitab-el-sawi.png'
import MikaeelPhoto from 'Shared/assets/images/other/mikaeel-khataw.png'
import KautherPhoto from 'Shared/assets/images/other/kaoutherakid.png'
import { t } from 'i18next'
import './TestimonialSection.scss'

class TestimonialSection extends Component {
  render () {
    return (
      <div>
        <div className='plus-testimonials__testimonial-container'>
          <div className='plus-testimonials__testimonial-mark' />
          <Slider
            dots
            infinite
            adaptiveHeight
            speed={1500}
            slidesToShow={1}
            slidesToScroll={1}
            arrows={false}
          >
            <div>
              <div className='plus-testimonials__testimonial-description'>
                {t('olivPlus.testimonialsSection.testimonial1.description')}
              </div>
              <div>
                <img src={NouranPhoto} className='plus-testimonials__testimonial-image' />
                <div className='plus-testimonials__testimonial-name'>{t('olivPlus.testimonialsSection.testimonial1.author')}</div>
                <div className='plus-testimonials__testimonial-job-details'>{t('olivPlus.testimonialsSection.testimonial1.jobDetails')}</div>
              </div>
            </div>

            <div>
              <div className='plus-testimonials__testimonial-description'>
                {t('olivPlus.testimonialsSection.testimonial2.description')}
              </div>
              <div>
                <img src={JonathanPhoto} className='plus-testimonials__testimonial-image' />
                <div className='plus-testimonials__testimonial-name'>{t('olivPlus.testimonialsSection.testimonial2.author')}</div>
                <div className='plus-testimonials__testimonial-job-details'>{t('olivPlus.testimonialsSection.testimonial2.jobDetails')}</div>
              </div>
            </div>

            <div>
              <div className='plus-testimonials__testimonial-description'>
                {t('olivPlus.testimonialsSection.testimonial3.description')}
              </div>
              <div>
                <img src={MahitabPhoto} className='plus-testimonials__testimonial-image' />
                <div className='plus-testimonials__testimonial-name'>{t('olivPlus.testimonialsSection.testimonial3.author')}</div>
                <div className='plus-testimonials__testimonial-job-details'>{t('olivPlus.testimonialsSection.testimonial3.jobDetails')}</div>
              </div>
            </div>

            <div>
              <div className='plus-testimonials__testimonial-description'>
                {t('olivPlus.testimonialsSection.testimonial4.description')}
              </div>
              <div>
                <img src={MikaeelPhoto} className='plus-testimonials__testimonial-image' />
                <div className='plus-testimonials__testimonial-name'>{t('olivPlus.testimonialsSection.testimonial4.author')}</div>
                <div className='plus-testimonials__testimonial-job-details'>{t('olivPlus.testimonialsSection.testimonial4.jobDetails')}</div>
              </div>
            </div>

            <div>
              <div className='plus-testimonials__testimonial-description'>
                {t('olivPlus.testimonialsSection.testimonial5.description')}
              </div>
              <div>
                <img src={KautherPhoto} className='plus-testimonials__testimonial-image' />
                <div className='plus-testimonials__testimonial-name'>{t('olivPlus.testimonialsSection.testimonial5.author')}</div>
                <div className='plus-testimonials__testimonial-job-details'>{t('olivPlus.testimonialsSection.testimonial5.jobDetails')}</div>
              </div>
            </div>
          </Slider>
        </div>
      </div>
    )
  }
}

export default TestimonialSection



