import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import { fetchCurrentLocationIfNeeded } from 'Shared/ducks/geolocation'
import {
  getCandidateFaqsForCurrentLocation
} from 'Shared/selectors/geolocation'
import FAQ from 'Shared/components/FAQ/FAQ'

const mapStateToProps = state => ({
  faqs: getCandidateFaqsForCurrentLocation(state)
})

const mapDispatchToProps = dispatch => ({
  fetchCurrentLocationIfNeeded: () => dispatch(fetchCurrentLocationIfNeeded())
})

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(FAQ))



