import { connect } from 'react-redux'
import OlivPlusLandingPage from 'Candidate/views/OlivPlus/OlivPlusLandingPage'
import { fetchOlivPlusMembershipPlan } from 'Shared/ducks/membership'
import { getActiveOlivPlusPlan, hasOlivPlusMembership } from 'Shared/selectors/membership'
import { getOlivPlusBasePrice, getPaymentCurrency } from 'Shared/selectors/geolocation'
import { getPriceWithCurrency } from 'Shared/helpers/payment'

const mapStateToProps = state => ({
  olivPlusPlan: getActiveOlivPlusPlan(state),
  olivPlusFormattedPrice: getPriceWithCurrency(getOlivPlusBasePrice(state), getPaymentCurrency(state)),
  hasMembership: hasOlivPlusMembership(state)
})

const mapDispatchToProps = dispatch => ({
  fetchOlivPlusMembershipPlan: () => dispatch(fetchOlivPlusMembershipPlan())
})

export default connect(mapStateToProps, mapDispatchToProps)(OlivPlusLandingPage)



