import React, { Component } from 'react'
import mixpanel from 'mixpanel-browser' // Import mixpanel if not already imported
import './AccordionPanel.scss'

class AccordionPanelHeader extends Component {
  state = {
    isOpen: false
  };

  toggle = () => {
    if (!this.state.isOpen) {
      mixpanel.track('Button Click - Add Promo Code')
    }

    this.setState({ isOpen: !this.state.isOpen })
  };

  render () {
    return (
      <div className='collapse-panel__header' onClick={this.toggle}>
        <span className='collapse-panel__header-content'>{this.props.children}</span>
        <div className='collapse-panel__header-expand-icon'>
          <span />
          <span style={{ transform: `rotate(${!this.state.isOpen ? 90 : 180}deg)` }} />
        </div>
      </div>
    );
  }
}

export default AccordionPanelHeader



