import React, { Component } from 'react'
import withTranslations from 'Shared/hoc/withTranslations'
import { withRouter } from 'react-router-dom'
import './OlivPlusLandingPage.scss'
import Button from 'Shared/components/Button'
import WelcomeSection from 'Candidate/views/OlivPlus/components/WelcomeSection/WelcomeSection'
import BenefitsSection from 'Candidate/views/OlivPlus/components/BenefitsSection/BenefitsSection'
import TestimonialSection from 'Candidate/views/OlivPlus/components/TestimonialSection/TestimonialSection'
import CompaniesSection from 'Candidate/views/OlivPlus/components/CompaniesSection/CompaniesSection'
import FAQConnectorCandidate from 'Candidate/connectors/FAQConnectorCandidate'
import mixpanel from 'mixpanel-browser'

class OlivPlusLandingPage extends Component {
  componentDidMount () {
    this.props.fetchOlivPlusMembershipPlan()
    mixpanel.track("Page View - Oliv Plus")
  }

  render () {
    const { t, olivPlusFormattedPrice, hasMembership } = this.props
    return (
      <section>
        <div className='oliv-plus-container parsing--hidden'>
          <div className='container'>
            <div className='oliv-plus-page-mobile__padding'>
              <WelcomeSection hasMembership={hasMembership} formattedPrice={olivPlusFormattedPrice} />
              <BenefitsSection />
              <TestimonialSection />
              <CompaniesSection />
            </div>
          </div>
          {
            !hasMembership && <div className='oliv-plus__cta-section--container'>
              <div className='oliv-plus__cta-section--header'>{t('olivPlus.ctaSection.header')}</div>
              <div className='oliv-plus__cta-section--button'>
                <Button
                  onClick={() => {this.props.history.push('/candidate/oliv-plus/review'); mixpanel.track("Button Click - Get Oliv Plus");}}
                  className='button button--primary hero-section__submit-input-btn'
                >
                  {t('olivPlus.welcomeSection.getOlivPlus', { formattedPrice: olivPlusFormattedPrice })}
                </Button>
              </div>
              <div>
                {t('olivPlus.ctaSection.subscribeToday')}
              </div>
            </div>
          }
          <div className='container'>
            <div className='oliv-plus-page-mobile__padding'>
              <FAQConnectorCandidate />
              <div className='oliv-plus__help-center-container'>
                <div>
                  <span>{t('olivPlus.checkHelpCenter')} </span>
                  <a
                    className='oliv-plus__help-center-link'
                    href='https://help.oliv.com'> {t('olivPlus.helpCenter')}
                  </a>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
    )
  }
}

export default withRouter(withTranslations(OlivPlusLandingPage))



