import React, { Component } from 'react'
import { t } from 'i18next'
import JobsIcon from 'Shared/assets/images/vectors/plus-approved.svg'
import MessageIcon from 'Shared/assets/images/vectors/plus-open-email.svg'
import './BenefitsSection.scss'

class BenefitsSection extends Component {
  render () {
    return (
      <div className='oliv-plus__benefits'>
        <div className='oliv-plus__benefits-heading'>
          {t('olivPlus.benefitsSection.header')}
        </div>
        <div className='oliv-plus__benefits-section'>
          <div className='columns'>
            <div className='column col-6 col-md-6 col-sm-12 oliv-plus__benefit-item'>
              <img className='oliv-plus__icon' src={JobsIcon} />
              <h3 className='oliv-plus__benefits-advantage'>
                {t('olivPlus.benefitsSection.boostApplicationsTitle')}
              </h3>
              <p className='oliv-plus__benefits-response'>
                {t('olivPlus.benefitsSection.boostApplicationsDesc')}
              </p>
            </div>
            <div className='column col-6 col-md-6 col-sm-12 oliv-plus__benefit-item'>
              <img className='oliv-plus__icon' src={MessageIcon} />
              <h3 className='oliv-plus__benefits-advantage'>
                {t('olivPlus.benefitsSection.messageEmployersTitle')}
              </h3>
              <p className='oliv-plus__benefits-response'>
                {t('olivPlus.benefitsSection.messageEmployersDesc')}
              </p>
            </div>
          </div>
        </div>
      </div>
    )
  }
}

export default BenefitsSection



