import React, { Component } from 'react'
import { withRouter } from 'react-router-dom'
import withTranslations from 'Shared/hoc/withTranslations'
import Button from 'Shared/components/Button'
import heroImage from 'Shared/assets/images/illustrations/oliv-plus-main-image.svg'
import './WelcomeSection.scss'
import ModalPlayer from 'Shared/components/ModalPlayer/ModalPlayer'
import { t } from 'i18next'
import { OLIV_PLUS_VIDEO_URL } from 'Shared/constants/Global'
import mixpanel from 'mixpanel-browser'

class WelcomeSection extends Component {
  render () {
    const { hasMembership, history, formattedPrice } = this.props

    const redirectToPath = path => () => history.push(path)

    return (
      <div className='welcome-section__container'>
        <div className='columns'>
          <div className='column col-6 col-sm-12'>
            <div className='welcome-section__heading-container'>
              <div className='welcome-section__heading'>
                {t('olivPlus.welcomeSection.header')}
              </div>
              <div className='welcome-section__description'>
                {t('olivPlus.welcomeSection.description')}
              </div>
              {hasMembership
                ? <div>
                  <Button
                    className='button welcome-section__button-disabled'
                  >
                    {t('olivPlus.welcomeSection.isActive')}
                  </Button>
                  <Button
                    secondary
                    onClick={redirectToPath('/candidate/settings/membership-details')}
                    className='button button--primary button--outline welcome-section__button-secondary'
                  >
                    {t('olivPlus.welcomeSection.membershipDetailsButton')}
                  </Button>
                </div>
                : <div className='column col-12 col-sm-12 col-md-12 welcome-section__button-container'>
                  <Button
                    onClick={() => {redirectToPath('/candidate/oliv-plus/review'); mixpanel.track("Button Click - Get Oliv Plus");}}
                    className='button button--primary welcome-section__submit-input-btn'
                  >
                    {t('olivPlus.welcomeSection.getOlivPlus', { formattedPrice: formattedPrice })}
                  </Button>
                </div>
              }
              {hasMembership
                ? <div className='welcome-section__hint-container'>
                  <p className='welcome-section__hint-title'>
                    {t('olivPlus.welcomeSection.needSupport')}
                  </p>
                  <div className='welcome-section__hint-support'>
                    <span>{t('olivPlus.welcomeSection.getInTouch')} </span>
                    <a href='mailto:team@oliv.com'>team@oliv.com</a>
                  </div>
                </div>
                : <div className='welcome-section__hint-container'>
                  <div className='welcome-section__hint-content'>
                    <span>{t('olivPlus.welcomeSection.howItWorks')}</span>
                    <ModalPlayer
                      videoUrl={OLIV_PLUS_VIDEO_URL}
                      customTrigger={
                        <p className='welcome-section__hint--video-trigger'>{t('olivPlus.welcomeSection.watchVideo')}</p>
                      }
                    />
                  </div>
                </div>
              }
            </div>
          </div>
          <div className='column col-6 col-sm-12'>
            <div className='welcome-section__image-container'>
              <img src={heroImage} alt='Hero image' />
            </div>
          </div>
        </div>
      </div>
    )
  }
}

export default withRouter(withTranslations(WelcomeSection))



